.header {
    position: relative;
    overflow: hidden;

    &-content {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        padding-top: 50px;

        max-width: 100%;
        height: 70vh;
    }

    &-text {
        max-width: 80%;
        text-align: start;
        position: relative;

        @include _md {
            padding-top: 0;
        }
    }

    &-title {
        font-size: 9em;
        margin-bottom: 16px;
        font-weight: 700;
        font-family: $ff-bebas, cursive;
        color: #ffffff;

        -webkit-transition: all 0.5s;
        -moz-transition: all 0.5s;
        transition: all 0.5s;

        @include _lg {
            font-size: 7em;
        }

        @include _md {
            font-size: 4em;
        }
    }

    &-subtitle {
        font-family: $ff-2p, serif;
        color: #ffffff;
        font-size: 30px;

        @include _md {
            font-size: 15px;
        }

        @include _sm {
            font-size: 16px;
        }
    }

    @import "./navbar.scss";
}

.navbar-wrapper {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 20px;
    transition: 0.4s;
    transition: 2s;

    @include _md {
        position: fixed;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
        height: 100vh;
        width: 50%;
        background-color: #000000;
        z-index: 4;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        right: -60vw;
        transition: 2s;

        animation: disactivator_md 0.4s;
        animation-fill-mode: forwards;

        padding: 20px 15px;

        &.active {
            right: 0vw;
            transition: 2s;
        }
    }
}

.navbar-list {
    display: flex;
    justify-content: center;
    align-items: center;

    @include _md {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding-top: 50px;
    }
}

.navbar-list-item {
    font-size: 25px;
    font-family: $ff-bebas, sans-serif;
    padding-left: 30px;

    @include _md {
        position: relative;
        font-size: 25px;
        font-size: 16px;
        padding-top: 20px;
        padding-bottom: 20px;
    }

    @include _sm {
        font-size: 40px;
    }
}

.navbar-link {
    color: #ffffff;
}

.navbar-link:hover {
    color: $color-estetic;
    text-decoration: none;
}

[data-navbar-toggler] {
    // d:none
    display: inline-block;
    // ml:a
    margin-left: auto;

    @include _md {
        display: inline-block;
    }
}
