.loader {
    position: fixed;
    height: 100vh;
    width: 100vw;
    left: 0;
    top: 0;
    background-color: #000000;
    z-index: 5;
    visibility: visible;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &.done {
        visibility: hidden;
        opacity: 0;
        transition: 1s;
    }

    .logo {
      color: $color-estetic;
      text-transform: uppercase;
    }

    &-icon {
      margin-bottom: 20px;
    }
}

.dank-ass-loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  
  .row {
     display: flex;
  }
}

$color: $color-estetic;
$size: 40px;
$time: 1;

.line {
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.arrow {
  width: 0; 
  height: 0;
  margin: 0 (-$size / 2);
  border-left: $size solid transparent;
  border-right: $size solid transparent;
  border-bottom: ($size * 1.8) solid $color;
  animation: blink $time + s infinite;
  filter: drop-shadow(0 0 ($size * 1.5) $color);
   
  &.down {
     transform: rotate(180deg);
  }
  
  @for $i from 1 through 18 {
     &.outer-#{$i} {
        animation-delay: -($time / 18) * $i + s;
     }  
  }

  @for $i from 1 through 6 {
     &.inner-#{$i} {
        animation-delay: -($time / 6) * $i + s;
     }  
  }  
}

@keyframes blink {
 0% { opacity: 0.1; }
 30% { opacity: 1; }
 100% { opacity: 0.1; }
}