.home-skills {
    text-align: center;
    background-color: #ffffff;
    padding-top: 70px;
    padding-bottom: 70px;

    &-card {
        width: 100%;
        padding-top: 10px;
        padding-bottom: 10px;

        &-icon {
            font-size: 50px;
            color: $color-estetic;
            margin-bottom: 20px;
        }

        &-title {
            font-family: $ff-bebas, cursive;
            text-transform: uppercase;
        }

        &-text {
            font-family: $ff-bebas, serif;
        }
    }
}