.home-whatido {
    padding-top: 30px;
    padding-bottom: 30px;
    min-height: 20vh;

    overflow: hidden; // added for pseudo-element
    position: relative; // added for pseudo-element
    background-image: linear-gradient(to bottom, rgb(0, 0, 0), rgba(247, 0, 255, 0.5));

    // Fixed-position background image
    &::before {
        content: " ";
        position: fixed; // instead of background-attachment
        width: 100%;
        height: 100%;
        bottom: 0;
        left: 0;
        background: url('../../../media/photos/skills-bg.jpg');
        background-size: cover;
        will-change: transform;
        z-index: -2;

        @include _lg {
            position: absolute;
        }
    }

    .whatido-carousel {
        position: relative;
        width: 100%;

        &-border {
            border-top: 5px solid $color-estetic;
            border-bottom: 5px solid $color-estetic;
        }

        &-buttons {
            width: 100%;
            display: flex;
            justify-content: space-between;
            position: absolute;
            top: 45%;

            @include _lg {
                top: 75%;
            }

            &-control {
                background-color: rgba(0, 0, 0, 0);
                padding: 15px;
                color: $color-estetic;
                font-size: 50px;
                margin: 0 50px;
                cursor: pointer;

                @include _lg {
                    margin: 20px;
                }

                @include _sm {
                    font-size: 40px;
                }
            }
        }

        &-dots {
            width: 100%;
            display: flex;
            justify-content: center;

            position: absolute;
            bottom: 5%;

            .whatido-carousel-dot {
                width: 15px;
                height: 15px;
                border: 2px solid $color-estetic;
                background-color: rgba(0, 0, 0, 0.5);
                border-radius: 50%;
                margin: 10px;

                cursor: pointer;

                &.active {
                    -webkit-animation-name: color-fade;
                    -webkit-animation-duration: 0.3s;
                    animation-name: color-fade;
                    animation-duration: 0.3s;
                    background-color: $color-estetic;

                    @-webkit-keyframes color-fade {
                        from {
                            background-color: rgba(0, 0, 0, 0.5);
                        }
                        to {
                            background-color: $color-estetic;
                        }
                    }
                    @keyframes color-fade {
                        from {
                            background-color: rgba(0, 0, 0, 0.5);
                        }
                        to {
                            background-color: $color-estetic;
                        }
                    }
                }
            }
        }
    }

    .whatido-card {
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding-top: 30px;
        padding-bottom: 30px;
        min-height: 65vh;

        &.active {
            -webkit-animation-name: fade;
            -webkit-animation-duration: 2s;
            animation-name: fade;
            animation-duration: 2s;
            @-webkit-keyframes fade {
                0% {
                    opacity: 0.1;
                }
                50% {
                    opacity: 0.5;
                }
                100% {
                    opacity: 1;
                }
            }

            @keyframes fade {
                0% {
                    opacity: 0.1;
                }
                50% {
                    opacity: 0.5;
                }
                100% {
                    opacity: 1;
                }
            }
        }

        @include _md {
            min-height: 70vh;
        }

        @include _320 {
            min-height: 100vh;
        }

        &-icon {
            color: #ffffff;
            font-size: 120px;
            padding-bottom: 15px;

            @include _sm {
                font-size: 80px;
            }
        }

        &-subtitle {
            color: #ffffff;
            font-size: 50px;
            text-transform: uppercase;
            font-family: $ff-monoton, cursive;

            @include _sm {
                font-size: 30px;
            }
        }

        &-text {
            color: #ffffff;
            font-size: 30px;
            font-family: $ff-audiowide, cursive;

            @include _sm {
                font-size: 16px;
            }
        }
    }
}
