.about-me {
    position: relative;
   /*  background: url('../../../media/photos/about-me-bg.jpg') center center / cover; */

   background-image: linear-gradient(to bottom, rgba(247, 0, 255, 0.3), rgb(0, 0, 0)), url('../../../media/photos/header-bg.jpg');
   -webkit-background-size: cover;
   -moz-background-size: cover;
   -o-background-size: cover;
   background-size: cover;
   background-position: top center;

    @include _lg {
        background-attachment: unset;
    }

    overflow-x: hidden;
    @import './AboutMeFlyingObjects.scss';

    text-align: center;
    padding-top: 75px;
    padding-bottom: 75px;
    background-size: contain;

    &-title {
        font-family: $ff-audiowide, cursive;
        color: #ffffff;
        font-size: 80px;
        z-index: 4;

        @include _sm {
            font-size: 40px;
        }
    }
    
    &-subtitle {
        font-size: 40px;
        color: #ffffff;
        font-family: $ff-audiowide, cursive;

        @include _sm {
            font-size: 16px;
        }
    }
    

    
    &-card {
        position: relative;

        .row {
            align-items: center;
        }
        
        

        &-information {
            width: 100%;
            text-align: left;
            min-height: 60vh;
            background-color: #ffffff;
            border-radius: 10px;
            padding: 20px;
            margin-bottom: 20px;

            @include _sm {
                min-height: 40vh;
            }
        }

        &-list {
            padding: 10px;
            &-item {
                margin-bottom: 20px;
                font-family: $ff-2p, serif;
                color: #000000;
                font-size: 20px;

                @include _sm {
                    font-size: 12px;
                    margin-bottom: 5px;
                }
            }
        }

        &-photo {
            display: flex;
            justify-content: center;
            align-items: center;height: 100%;
            

            @include _600 {
                height: 150px;
            }
        }
    }

    .home-aboutme-form-text {
        color: #ffffff;
        font-size: 30px;
        font-family: $ff-bebas, cursive;
    }


    &-form {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        &-input {
            background-color: rgba(0, 0, 0, 0);
            padding: 20px;
            outline: none;
            box-shadow: none;
            border: 3px solid rgba(255, 255, 255, 0.548);
            color: #ffffff;
            border-radius: 10px;
            margin-bottom: 25px;

            &::placeholder {
                color: rgba(255, 255, 255, 0.404);
                font-size: 24px;
                font-family: $ff-geo, serif;
            }


            @include _sm {
                margin-bottom: 20px;
            }
        }

        &-submit {
            background-color: $color-estetic;
            color: #ffffff;
            border: 1px solid rgba(255, 255, 255, 0.548);
            padding: 10px 60px;

            span {
                font-family: $ff-bebas, serif;
                color: #ffffff;
                font-size: 24px;
            }

            
        }
    }

}
