.home-quotes {
    min-height: 90vh;
    background-image: linear-gradient(to bottom, rgb(255, 255, 255), rgb(230, 230, 230));
    background-color: #ffffff;
    overflow: hidden;

    &-carousel {
        min-height: 90vh;
        position: relative;

        &-card {
            position: relative;
            display: flex;

            @include _lg {
                flex-direction: column;
            }

            &-overlay {
                position: absolute;
                width: 100%;
                height: 100%;
                z-index: -10;
                background-color: rgba(0, 0, 0, 0);

                &.active {
                    z-index: 1;
                    background-color: #ffffff;
                    animation: to-white 2s;

                    @keyframes to-white {
                        0% {
                            background-color: rgba(0, 0, 0, 0);
                        }
                        50% {
                            background-color: #ffffff;
                        }
                        100% {
                            background-color: rgba(0, 0, 0, 0);
                        }
                    }
                }
            }
        }

        &-dots {
            display: flex;
            position: absolute;
            right: 10%;
            bottom: 10%;

            @include _lg {
                top: 3%;
                left: 70%;
            }

            @include _xs {
                left: 65%;
            }

            .home-quotes-carousel-dot {
                width: 15px;
                height: 15px;
                border-radius: 50%;
                background-color: transparent;
                margin-right: 5px;
                border: solid 2px #000000;
                cursor: pointer;

                &.active {
                    background-color: $color-estetic;
                }
            }

            .quotes-prev-button {
                height: 20px;
                width: 5px;
                background-color: #000000;
                margin-right: 5px;
            }
            .quotes-next-button {
                height: 20px;
                width: 5px;
                background-color: #000000;
            }
        }

        &-img {
            height: 90vh;
            background-color: #ffffff;
            position: relative;

            @include _lg {
                height: 100%;
            }

            .overlay {
                position: absolute;
                background-color: rgba(247, 0, 255, 0.3);
                width: 100%;
                height: 100%;
            }

            img {
                width: 50vw;
                height: 100%;

                @include _lg {
                    width: 100vw;
                }
            }
        }

        &-content {
            padding: 80px 80px;
            font-family: $ff-bebas, cursive;
            position: relative;

            @include _md {
                padding: 50px 50px;
            }

            &-title {
                font-size: 40px;

                &::before {
                    content: "";
                    display: block;
                    width: 20px;
                    border-bottom: 4px solid $color-estetic;
                    margin-bottom: 20px;
                }
            }

            &-subtitle {
                font-size: 30px;

                &::before {
                    content: "";
                    display: block;
                    width: 20px;
                    border-bottom: 4px solid $color-estetic;
                    margin-bottom: 20px;
                }
            }

            &-text {
                font-size: 24px;

                @include _lg {
                    font-size: 20px;
                }

                &::before {
                    content: "";
                    display: block;
                    width: 100px;
                    border-bottom: 4px solid $color-estetic;
                    margin-bottom: 20px;
                }
            }
        }
    }
}
