.home-header {
  height: 90vh;
  position: relative;
  overflow: hidden;
  position: relative;
  background: url("../../../media/photos/header-bg.jpg") no-repeat left center / cover;
  background-attachment: fixed;

  @include _lg {
      background-attachment: unset;
  }

  &-overlay {
    width: 100%;
    height: 100%;
    background-color: rgba(247, 0, 255, 0.3);
    position: absolute;
  }

  &-background-2 {
    position: absolute;
    width: 100vw;
    height: 140vh;
    top: -40%;
    left: 0;
    visibility: hidden;
    opacity: 0;
    animation: visible 2s;
    animation-delay: 2.2s;
    animation-fill-mode: forwards;
  }

  @keyframes visible {
    to {
      opacity: 1;
      visibility: visible;
    }
  }
  .scroll-thing {
    visibility: hidden;
    opacity: 0;
    &.active {
      opacity: 1;
      visibility: visible;
      transition: 2s;
    }
  }
  
  
